header li img {
  width: 70px;
}

header .bannerflex {
  display: flex;
  flex-direction: row;
  align-items: flex-end; /* Aligner les éléments en bas */
  justify-content: space-between;
  flex-grow: 1;
  height: 90%; /* Assurez-vous que le conteneur prend toute la hauteur disponible */
}

header nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: #a3ba9c;
  color: white;
  height: 100px;
  top: 0;
  z-index: 1000;
  padding: 0 35px;
}

header nav ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
  
}

header nav li {
  flex: 1;
  text-align: center;
}

header nav li a {
  text-decoration: none;
  color: #005431;
  font-weight: bold;
  padding: 8px;
  border-radius: 5px;
  background-color: #a3ba9c;
  transition: background-color 0.3s ease;
  margin-right: 15px;
}

header nav li a:hover {
  background-color: #005431;
  color: #f0f7f2;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  
}





/* Styles pour l'icône profil */
.avatar {
  width: 45px;
  height: 45px;
  border: 3px solid #000;
  border-radius: 50%;
  transition: 0.3s;
}

.avatar:hover {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}




/*Switch DarkMode*/
.modeswitch{
  display: flex;
  align-items: center;
  justify-content: center;
}

.switch{
  width: 50px;
  height: 18px;
  background: #2d2d2d;
  border-radius: 30px;
  display: flex;
  align-items: center;
}

#mode{
  display: none;
}

#sun{
  font-size: 10px;
  color:#bdbdbd;
  margin:0px 0px 0px 3px;
}

#moon{
  font-size: 10px;
  color:#bdbdbd;
  margin:0px 5px 0px 25px;
}

.switch::before{
  position: absolute;
  content: "";
  padding: 8px;
  background: #bdbdbd;
  border-radius: 50%;
  transition: 0.3s;
  transform: translateX(-0.5px);
  border: 3px solid #656565;
}

#mode:checked ~ .switch::before{
  transform: translateX(33px);
  background: #484848;
}

/* Dark Mode */
body.dark header nav li a {
  background-color: #86b47800;
}

body.dark header nav li a:hover {
  background-color: #005431;
}

body.dark header nav {
  background-color: #86b478;
}

