/* Styles pour le menu responsive */
@media screen and (max-width: 600px) {
  
  .bannerflex li:not(.iconburger) {
    display: none;
  }

  .bannerflex .responsive li {
    display: block;
  }

  .iconburger {
    display: block;
    order: -1;
  }
}

