.burger-menu {
    position: relative;
    display: inline-block;
  }
  
  .iconburger {
    cursor: pointer;
    font-size: 24px;
    color: #333;
    display: none; /* Caché par défaut sur les grands écrans */
  }
  
  .menu-dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #fff;
    border: 1px solid #ccc;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    min-width: 200px;
    display: none; /* Caché par défaut */
  }
  
  .menu-dropdown ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  .menu-dropdown li {
    padding: 10px;
    border-bottom: 1px solid #eee;
  }
  
  .menu-dropdown li:last-child {
    border-bottom: none;
  }
  
  .menu-dropdown a {
    text-decoration: none;
    color: #333;
    display: flex;
    align-items: center;
  }
  
  .menu-dropdown a .icon {
    margin-right: 10px;
  }
  
  /* Afficher le menu déroulant lorsque le menu est ouvert */
  .burger-menu.open .menu-dropdown {
    display: block;
  }
  
  /* Mode sombre pour le menu déroulant */
  body.dark .menu-dropdown {
    background-color: #333;
    border-color: #444;
  }
  
  body.dark .menu-dropdown a {
    color: #fff;
  }
  
  body.dark .menu-dropdown li {
    border-bottom-color: #444;
  }
  
  /* Responsive design */
  @media (min-width: 601px) {
    .burger-menu{
        display: none;
    }
  }

  @media (max-width: 600px) {
    .iconburger {
      display: block; /* Afficher l'icône du burger sur les petits écrans */
    }
  
    .left-container {
      display: none; /* Cacher le menu de gauche sur les petits écrans */
    }
  
    .menu-box.responsive {
      display: block; /* Afficher le menu déroulant à la place */
    }
  }