@import url(https://fonts.googleapis.com/css?family=Ubuntu:400,700);
@import url(https://weloveiconfonts.com/api/?family=entypo|fontawesome|zocial);
@import url('https://cdn.jsdelivr.net/npm/bootstrap-icons/font/bootstrap-icons.css');


/*menu a gauche*/

.left-container {
    flex: 0 0 250px;
    margin: 35px;
}
.left-container ul {
    list-style-type: none;
    margin: 0;
    padding-left: 0;
}

.menu-box {
  display: block; 
  background-color: #a3ba9c;
  border-radius: 10px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}
.menu-box .titular {
    background: #005431;
    color: #fff;
    text-align: center;
    margin: 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    font-size: 1.5em;

}
.menu-box-menu .icon {
    width: 24px;
    margin-left: 20px;
    margin-right: 15px;
    margin-top: 10px;
}
/*POUR PLUS TARD LES NOTIFICATIONS*/
.menu-box-number {
    width: 22px;
    line-height: 22px;
    background: #ea3232;
    text-align: center;
    border-radius: 20px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    position: relative;
    left: 20%;
    font-size: 0.8;
    
    animation: bounce 0.5s ease-out;
}
.menu-box-number span {
    position: relative;
    top: 2px;
    color: #fff;
}
/* Animation de rebond */
@keyframes bounce {
    0% {
        transform: scale(0);
    }
    60% {
        transform: scale(1.5);
    }
    100% {
        transform: scale(1);
        
    }
}

.menu-box-tab:hover .menu-box-number {
    background: #d10a0a;
}

.menu-box-tab {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    /* Centrage vertical */
    color: #fff;
    font-size: 1em;
    line-height: 60px;
    height: 60px;
    /* Fixer la hauteur */
    transition: background 0.3s ease, color 0.3s ease, font-size 0.3s ease, line-height 0.3s ease;
}
.menu-box-tab:hover {
    color: #fff;
    font-size: calc(1em + 0.1em);
    line-height: calc(60px + 3px);
    /* Ajuste la hauteur pour suivre l'agrandissement */
    background: #005431;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-decoration: none;
}


/****************************/
/* Menu à gauche en mode sombre */
body.dark .menu-box {
    background-color: #86b478;
  }
  
  body.dark .menu-box .titular {
    background-color: #005431;
  }
  
  body.dark .menu-box-tab {
    color: #ffffff;
  }
  
  body.dark .menu-box-tab:hover {
    background-color: #005431;
  }


 /*****************************/
 
 
@media screen and (max-width: 600px) {


    .menu-box {
        display: none; /* Masquer le menu par défaut sur les petits écrans */
    }
  
    .menu-box.responsive {
        display: block; /* Afficher le menu lorsque la classe 'responsive' est appliquée */
        position: absolute;
        top: 100px;
        left: 0;
        width: 100%;
        background-color: #a3ba9c;
        z-index: 1000;
        border-radius: 0;
        box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    }


}