@import url('https://fonts.googleapis.com/css2?family=Dongle&display=swap');

.connexion-page {
  background: #f6f5f7; 
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 0.8em;
  height: 100vh;
}

.h1-container {
  font-size: 2.2em;
  font-weight: bold;
  color : #005431;
  margin: 0;
}

.h1-panel{
  font-size: 2.5em;
  font-weight: bold;
  color:  #fff;
}

.connexion-page h2 {
  text-align: center;
}

.connexion-page p {
  font-size: 0.9em;
  font-weight: 100;
  line-height: 20px;
  letter-spacing: 0.5px;
  margin: 20px 0 30px;
}

.connexion-page span {
  font-size: 1em;
}

.connexion-page a {
  color: #333;
  font-size: 0.8em;
  text-decoration: none;
  margin: 10px 0;
}


.img_icons_sign {
  height: 90%; 
  width: 90%;
}

.connexion-page button {
  border-radius: 20px;
  border: 1px solid #005431;
  background-color: #005431;
  color: #FFFFFF;
  font-size: 0.8em;
  font-weight: bold;
  padding: 12px 45px;
  margin-top: 5px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

.connexion-page button:active {
  transform: scale(0.95);
}

.connexion-page button:focus {
  outline: none;
}

.connexion-page button.ghost {
  background-color: transparent;
  border-color: #FFFFFF;
}

.connexion-page form {
  background-color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

.connexion-page input {
  background-color: #f0f7f2;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
  border-radius: 5px;
}

.connexion-page .container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(240,247,242, 0.75), 0 10px 10px rgba(240,247,242, 0.72);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 650px;
}

.connexion-page .form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.connexion-page .sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.connexion-page .container.right-panel-active .sign-in-container {
  transform: translateX(100%);
}

.connexion-page .sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.connexion-page .container.right-panel-active .sign-up-container {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  animation: show 0.6s;
}

@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}

.connexion-page .overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.connexion-page .container.right-panel-active .overlay-container {
  transform: translateX(-100%);
}

.connexion-page .overlay {
  background: #005431;
  background: -webkit-linear-gradient(to right, #005431, #085435);
  background: linear-gradient(to right, #005431, #085435);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #FFFFFF;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.connexion-page .container.right-panel-active .overlay {
  transform: translateX(50%);
}

.connexion-page .overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.connexion-page .overlay-left {
  transform: translateX(-20%);
}

.connexion-page .container.right-panel-active .overlay-left {
  transform: translateX(0);
}

.connexion-page .overlay-right {
  right: 0;
  transform: translateX(0);
}

.connexion-page .container.right-panel-active .overlay-right {
  transform: translateX(20%);
}

.connexion-page .social-container {
  margin: 5px 0;
}

.connexion-page .social-container a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}


@media screen and (max-width: 768px) {
  .connexion-page .container {
    width: 90%;
    min-height: 350px;
  }

  .connexion-page .overlay {
    width: 200%;
  }

  .connexion-page .overlay-panel {
    padding: 0 20px;
  }

  .connexion-page input {
    width: 80%;
    margin: 8px auto;
  }

  footer {
    font-size: 12px;
    padding: 10px;
  }
}


body.dark .connexion-page {
  background: #282E2E;
}

body.dark .connexion-page  a{
  color: #282E2E;
}

.connexion-page .container {
  color: #282E2E;
  
  box-shadow: 0 14px 28px #1e2222, 0 10px 10px #1e2222;

}

body.dark .connexion-page .container {
  color: #282E2E;
  
  box-shadow: 0 14px 28px rgba(255, 255, 255, 0.2), 0 10px 10px rgba(255, 255, 255, 0.15);

}
.name-container {
  display: flex;
  gap: 10px; 
  width: 100%;
}

.name-container input {
  flex: 1; 
}

.message-erreur {
  display: inline-block;
  background-color: #ffcccc; 
  color: #d8000c; 
  border: 1px solid #d8000c;
  padding: 0px 10px;
  border-radius: 5px;
  font-size: 8px;
  margin-top: 2px;
  max-width: 250px;
  text-align: center;
}