@import url('https://fonts.googleapis.com/css2?family=Dongle&display=swap');

body {
  margin: 0;
  font-family: "Dongle", serif;
  font-style: normal;
  font-size: 1.8em;
  line-height: 1.5;
  background-color: #f0f7f2;
}

.app-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* S'assure que la page occupe toute la hauteur de l'écran */
}


.main {
  flex: 1; /* Prend tout l'espace disponible entre le header et le footer */
}

ul{
  list-style: none;
}
/*************************************************/
/* Mode sombre pour le body */
/*************************************************/


body.dark {
  background-color: #282E2E;
  color: #ffffff;
}

/* Styles supplémentaires pour le mode sombre (exemple) */
body.dark .header {
  background-color: #86b478;
}

body.dark a {
  color: #ffffff;
}

body.dark .switch {
  background: #555;
}

body.dark #sun,
body.dark #moon {
  color: #ffffff;
}