.deconnection:hover{
    background-color: rgb(196, 0, 0);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.settings-container {
    display: flex;
    flex-direction: column;
    gap: 20px; /* Ajoute un espace entre les cartes */
}

.item-panier {
    margin-bottom: 15px; /* Ajoute un espace entre les éléments */
    background-color: #f0f7f2;
}


/* Styles pour le mode sombre */

body.dark .card {
    background-color: #2e3535;
    color: white;
}

body.dark label{
    color: white;
}

body.dark input {
    background-color: #444;
    color: white;
    border: 1px solid #666; 
}

body.dark .item-panier {
    background-color: #444;
    color: white;

}